import React, { Component } from 'react'
import './index.css'

export default class Duty extends Component {
    render() {
        return (
            <div>
                <div className="img">    
                    <img className="pic" src="media/全商背景.jpg" alt='圖片'/>
                </div>

            <article>
                <h2>嚴選食材</h2>
                <div className='duty'>
                    <div>
                        <div>澳洲牛肉<i className="fas fa-hamburger"></i></div>
                        <img src="media/6-1.jpg" alt='圖片' style={{width: 300}}/>
                        <div>台灣豬肉<i className="fas fa-piggy-bank"></i></div>
                        <img src="media/6-2.jpg" alt='圖片' style={{width: 300}}/>
                    </div>

                </div>
                <table id="abc">
                    <tbody>
                    <tr>
                        <td className="t1">澳洲牛肉<i className="fas fa-hamburger"></i></td>
                        <td className="t1">台灣豬肉<i className="fas fa-piggy-bank"></i></td>
                    </tr>
                    <tr id='tr1'>
                        <td><img src="media/6-1.jpg" alt='圖片' style={{width: 300}}/></td>
                        <td><img src='media/6-2.jpg' alt='圖片' style={{width: 520}}/></td>
                    </tr>
                    </tbody>
                </table>
            </article>
                
            </div>
        )
    }
}
