import React, { Component } from 'react'
import {withRouter,Route,Redirect} from 'react-router-dom'
import './index.css'
import Allroducts from '../../pages/Allroducts'
import Store from '../../pages/Store'
import Home from '../../pages/Home'
import Duty from '../../pages/Duty'
import Story from '../../pages/Story'
import Addus from '../../pages/Addus'
class Header extends Component {

    state={rollStyle:true}

    componentDidMount(){
       window.addEventListener('scroll',this.handleRoll)
    }

    handleRoll=()=>{
        //console.log(window.outerWidth) //視窗寬
        //console.log(window.scrollY) //滾動範圍
        if(window.scrollY>=50){
            //console.log('fixed')
            this.setState({rollStyle:false})
        }
        else{
            //console.log('static')
            this.setState({rollStyle:true})
        }
    }

    Goto=(e)=>{
        //console.log('點這跳到全部商品')
        this.props.history.push(`/${e}`)
       //console.log(e)
    }

    render() {
        const{rollStyle}=this.state

        return (
            <div>
            <div>
                {/*上邊黑線 */}
                <div ref={c=>this.box=c} className="box"/>
            </div>  

            <header style={{width:window.outerWidth}} className={rollStyle?'static':'fix'}> {/*目錄 */}
                <div onClick={()=>this.Goto('home')} className="box2">首頁</div>
                <div onClick={()=>this.Goto('allroducts')} className="box2">全部商品</div>
                <div onClick={()=>this.Goto('store')} className="box2">門市資訊</div>
                <div onClick={()=>this.Goto('duty')} className="box2">企業責任</div>
                <div onClick={()=>this.Goto('story')} className="box2">品牌故事</div>
                <div onClick={()=>this.Goto('addus')} className="box2">加入我們</div>
                <Redirect to='/home'/>
                
            </header>
            <Route path='/home' component={Home}/>
            <Route path='/allroducts' component={Allroducts}/>
            <Route path='/store' component={Store}/>
            <Route path='/duty' component={Duty}/>
            <Route path='/story' component={Story}/>
            <Route path='/addus' component={Addus}/>
            {/*右邊*/}
            <div className="float"> 
                 <img src="media/9優惠.png"  alt='圖片'/> 
             </div> 
            {/*左上logo*/}
            <div className="float2" onClick={()=>this.Goto('home')}> 
                <img src="media/logo.jpg" alt='圖片'/>
            </div> 
            
            </div>
        )
    }
}


export default withRouter(Header)