import React, { Component } from 'react'
import './index.css'

export default class Addus extends Component {
    render() {
        return (
            <div className='addus'>
                
                <div className="img">    
                    <img className="pic" src="media/全商背景.jpg" alt='...'/>
                </div>

            <article>
                    <div className="card mb-4" style={{maxWidth: 600,maxHeight:250,textAlign:'center',marginTop:40}} >
                    <div className="row g-0">
                        <div className="col-4 qrcode">
                        <img src="media/QRcode.jpg" alt="..." />
                        </div>
                        <div className="col-8 ">
                        <div className="card-body">
                        <h3 className="card-title"><br/><i className="fab fa-line" style={{color: 'green'}}></i><span>   </span>加入我們</h3><br/>
                        <p className="card-text">最新資訊一手掌握<span>   </span><i className="far fa-hand-spock fa-pulse" ></i><br/>歡迎揪團一起到APPLE GREEN COFFEE</p>
                        <p className="card-text"><small className="text-muted">蘋果綠咖啡官方LINE： @applegreencoffee</small></p>
                        </div>
                        </div>
                    </div>
                    </div>

                    <div className="abc">
                        <div className="d1">徵才公告</div>
                        <div><br/></div><div></div>
                        <div className="d2">職務名稱</div>
                        <div className="d3">早班正職</div>
                        <div><br/></div><div></div>
                        <div className="d2">需求人數</div>
                        <div className="d3">1-2人</div>
                        <div><br/></div><div></div>
                        <div className="d2">職類</div>
                        <div className="d3">門市店員/餐飲服務生</div>
                        <div><br/></div><div></div>
                        <div className="d2">職務說明</div>
                        <div className="d3">
                            我們重視與夥伴之間的真誠，期盼一起用心打拼開創未來。<br/>
                            讓我們將熱忱傳遞到顧客心中，每一天都是美好的APPLE DAY!<br/><br/>
                            
                            工作內容： 備餐、點餐、調製飲品為基本首要。<br/><br/>
                            
                            1.凡事精益求精的持續進步，維持店內飲品輕食品質。<br/>
                            2.對於團隊進步有想法及熱情的你，能適時協助夥伴共同完成目標。<br/>
                            3.愛乾淨，有禮貌，保持工作環境及店內整潔。<br/>
                            4.活潑且不害臊、勇於與顧客熱情的交流。<br/>
                            5.對餐飲有熱情，努力且有上進心。<br/>
                        </div>
                        <div><br/></div><div></div>
                        <div className="d2">員工福利</div>
                        <div className="d3">
                            1.勞健保及6%退休金提撥<br/>
                            2.團體意外傷害保險<br/>
                            3.月休八天<br/>
                            4.尾牙代金(符合資格者可領取)<br/>
                            5.特休假(依勞動法規)
                        </div>
                    </div>
                   
            </article>

            </div>
        )
    }
}
