import React, { Component } from 'react'
import './index.css'

export default class Home extends Component {

    state={
        imgs:["media/首頁.jpg","media/首頁2.jpg","media/首頁3.jpg","media/首頁4.jpg","media/首頁5.jpg"],
        imgsIndex:0
    }

    componentDidMount(){
        this.timer=
        setInterval(() => {
            const {imgs,imgsIndex}=this.state
            const{pic}=this
            if(imgsIndex<4)
            {this.setState({imgsIndex:imgsIndex+1})}
            else {this.setState({imgsIndex:0})}
            pic.src=imgs[imgsIndex]
        }, 2000);        
    }

    componentWillUnmount(){
        clearInterval(this.timer)
    }
    
    render() {
        return (
            <div className='home'>
                {/* 正中圖片 */}
                <article>
                    <img ref={c=>this.pic=c} id="pic" src="media/首頁.jpg" alt='圖片'/>
                </article>
                <section>
                    <img src="media/小圖1.jpg" alt='小圖片'/>
                    <img src="media/小圖2.jpg" alt='小圖片'/>
                    <img src="media/小圖3.jpg" alt='小圖片'/>
                    <img src="media/小圖9.jpg" alt='小圖片'/>
                    <img src="media/小圖5.jpg" alt='小圖片'/>
                    <img src="media/小圖4.jpg" alt='小圖片'/>
                    <img src="media/小圖7.jpg" alt='小圖片'/>
                    <img src="media/小圖8.jpg" alt='小圖片'/>
                </section>
            </div>
        )
    }
}
