import React, { Component } from 'react'
import FlipPage from 'react-flip-page'

import './index.css'


export default class Allroducts extends Component {

    render() {
        return (
            <div>
                <div className="img">    
                    <img className="pic" src="media/全商背景.jpg" alt='圖片'/>
                </div>
                <div className="Imagemenu">
                <FlipPage  orientation='horizontal' width='1000' height='650'>
                    <article>
                        <img src="media/1-1.jpg" alt='小圖片'/>
                    </article>
                    <article>
                        <img src="media/1-2.jpg" alt='小圖片'/>
                    </article>
                    <article>
                        <img src="media/1-3.jpg" alt='小圖片'/>
                    </article>
                    <article>
                        <img src="media/1-4.jpg" alt='小圖片'/>
                    </article>
                </FlipPage>
                </div>
            </div>
        )
    }
}
